.dialog-body {
  background: #fafafa;
  min-height: 100vh;
  padding-right: 0.9375em;
  padding-left: 0.9375em;
  color: #4a5468;
  display: flex;
}

.dialog-root {
  display: flex;
  flex-direction: column;
  background: #fafafa;
}

.content {
  flex: 1 1 auto;
  overflow-y: auto;
}

.steps {
  margin-bottom: 2.5em;
  overflow: hidden;
}
