export const getIntlMessage = async (locale: string): Promise<Record<string, string>> => {
  let messages: Record<string, string> = {};

  if (locale === 'en') {
    const response = await import(`../assets/locales/en.translation.json`);
    messages = response.default;
  } else if (locale === 'hu') {
    const response = await import(`../assets/locales/hu.translation.json`);
    messages = response.default;
  }

  return messages;
};

export const getIntlMessageFormatted = (message: string, values: string[]): string => {
  return message.replace(/{(\d+)}/g, (_match, index) => {
    return values[index] || '';
  });
};