:host {
  display: block;
}

.result-container {
  background: #e8f1f1;
  border: 1px solid #2b7371;
  border-radius: 2px;
  padding: 0.5625em;
  display: flex;
  flex-direction: column;
  margin-bottom: 1.25em;
}

.result-with-tag {
  display: flex;
  justify-content: space-between;
  margin-top: 0.625em;
}

.fade-in.top {
  transition: 0.5s all ease-in-out;
  transform: translate3d(0, -50px, 0);
  opacity: 0;
}

.fade-in.show {
  opacity: 1;
  transform: translate3d(0, 0, 0);
}
