import { Component, Element, Event, EventEmitter, Prop, State, Watch, h } from '@stencil/core';
import { Answer, Fragment } from '../../interfaces/dialog-interfaces';
import { getIntlMessage } from '../../language/translation';
import { popoverController } from '@ionic/core';

@Component({
  tag: 'step-component',
  styleUrl: 'step-component.css',
  shadow: false,
})
export class StepComponent {
  private messages: Record<string, string> = {};

  options: string[] = [];
  stringAnswerValue: string;
  numberAnswerValue: string;
  dateAnswerValue: string;
  localNumberAnswerValue: string;
  @State() localDateAnswerValue: string;

  @Element() private element: HTMLElement;

  @Prop() fragment: Fragment;
  @Prop({ mutable: true }) answerValue: any;
  @Prop() controlDisabled: boolean;
  @Prop() locale: string = 'hu';
  @Prop() lastLocale: string = '';

  @State() isActive: boolean = false;
  @State() isDateActive: boolean = false;
  @State() showContent = false;

  @Event() answer: EventEmitter<Answer>;
  @Event() revokeAnswer: EventEmitter<Answer>;


  @Watch('locale')
  async loadMessages() {
    if (this.locale !== this.lastLocale) {
      this.lastLocale = this.locale;
      this.messages = await getIntlMessage(this.locale);
    }
  }

  @Watch('answerValue')
  handleAnswerValueChange(newValue: string) {
    console.log("answerValue")
    this.numberAnswerValue = newValue + ''
    this.localNumberAnswerValue = newValue + '';
    this.dateAnswerValue = newValue;
    this.stringAnswerValue = newValue;
    this.yesButtonClass();
    this.noButtonClass();
    this.isActive = true;
    this.isDateActive = true;
  }

  @Watch('fragment')
  handleFragmentValueChange(newValue, oldValue) {
    console.log("old", oldValue)
    console.log("new", newValue)
    this.showContent = false;
    if (this.fragment.type === 'QuestionMenu') {
      this.options = [];
      for (let i = 0; i < this.fragment.optionsExpert.length; i++) {
        this.options.push(this.fragment.optionsLayman[i]);
      }
    }

    console.log("handleFragmentValueChange", this.options)
    setTimeout(() => {
      this.showContent = true;
      this.setFocus();
    }, 1000); // 200ms késleltetés*
    console.log("fragment")
  }


  async componentWillLoad() {
    this.showContent = false;
    if (this.answerValue) {
      this.numberAnswerValue = this.answerValue;
      this.localNumberAnswerValue = this.numberAnswerValue;
      this.dateAnswerValue = this.answerValue;
      this.stringAnswerValue = this.answerValue;
      this.yesButtonClass();
      this.noButtonClass();
    }

    if (this.fragment.type === 'QuestionMenu') {
      for (let i = 0; i < this.fragment.optionsExpert.length; i++) {
        this.options.push(this.fragment.optionsLayman[i]);
      }
    }
    setTimeout(() => {
      this.showContent = true;
      this.setFocus();
    }, 1000); // 200ms késleltetés
    console.log("componentWillLoad", this.options)
    await this.loadMessages();

  }



  componentDidLoad() {
    console.log("componentDidLoad")
    //this.setFocus();
  }

  setFocus() {
    setTimeout(() => {
      console.log("setFocus")
      const firstInput = this.element.querySelector('input');
      console.log("input", firstInput)
      if (!firstInput) {
        console.log(this.element)
      }

      if (!(this.answerValue !== null && this.answerValue !== undefined)) {
        console.log("focus", firstInput)
        if (firstInput) {
          firstInput.focus();
        } else {
          const firstButton = this.element.querySelector('button');
          if (firstButton) {
            firstButton.focus()
          }
        }


      }

    }, 1000)
  }

  render() {
    const svgWidth = '1.5em'; // A szélesség beállítása
    const svgColor = "#2B7371"; // A szín beállítása
    const svgViewBox = "0 0 512 512"
    const svgPathCalendar = "M152 24c0-13.3-10.7-24-24-24s-24 10.7-24 24V64H64C28.7 64 0 92.7 0 128v16 48V448c0 35.3 28.7 64 64 64H384c35.3 0 64-28.7 64-64V192 144 128c0-35.3-28.7-64-64-64H344V24c0-13.3-10.7-24-24-24s-24 10.7-24 24V64H152V24zM48 192H400V448c0 8.8-7.2 16-16 16H64c-8.8 0-16-7.2-16-16V192z"

    return (
      <div class="step">
        {this.fragment.type != 'Goal' && (
          <div>
            <step-question
              fragment={this.fragment}
              isActiveQuestion={!(this.answerValue !== null && this.answerValue !== undefined)}
            ></step-question>
            <div class={`answer fade-in right ${this.showContent ? 'show' : ''}`}>

              {this.fragment.type === 'QuestionMenu' && (
                <div class="speech-bubble-triangle">
                  <span class="speech-bubble-answer">
                    <div class="options-container">
                      {this.options.map((op, index) => (

                        <button class={{ "answer-button": true, "active": this.answerValue === index }}
                          disabled={this.controlDisabled}
                          onClick={() => this.menuAnswer(index)}
                        >
                          <div>{op}</div>
                        </button>
                      ))}
                    </div>
                  </span>
                  <div class="triangle-topright"></div>
                </div>
              )}
              {this.fragment.type === 'QuestionYesNo' && (
                <div class="speech-bubble-triangle">
                  <span class="speech-bubble-answer">
                    <button class={{ "answer-button": true, "active": this.yesButtonClass() }}
                      disabled={this.controlDisabled}
                      onClick={() => this.yesNoAnswer('true')}
                    >
                      {this.messages['question-yes']}
                    </button>
                    <button class={{ "answer-button": true, "active": this.noButtonClass() }}
                      disabled={this.controlDisabled}
                      onClick={() => this.yesNoAnswer('false')}
                    >
                      {this.messages['question-no']}
                    </button>

                  </span>
                  <div class="triangle-topright"></div>
                </div>
              )}
              {this.fragment.type === 'QuestionInteger' && (
                <div class="speech-bubble-triangle">
                  <span class="speech-bubble-answer">
                    <form onSubmit={(event) => {
                      event.preventDefault();
                      const form = event.target as HTMLFormElement;
                      this.numberAnswerValue = form.getElementsByTagName('input')[0].value
                      this.numberAnswer();
                    }}>
                      <input
                        type="number"
                        inputMode='numeric'
                        min="0" step="1"
                        class={{ "answer-input": true, "active": this.isActive }}
                        value={this.localNumberAnswerValue}
                        onInput={(event) => this.onNumberInput(event)}
                        disabled={this.controlDisabled}
                        onKeyDown={() => this.setNumberActive()}
                        style={{ width: '8em' }}
                      />
                      <input type="submit" hidden />
                    </form>
                  </span>
                  <div class="triangle-topright"></div>
                </div>
              )}
              {this.fragment.type === 'QuestionDecimal' && (
                <div class="speech-bubble-triangle">
                  <span class="speech-bubble-answer">
                    <input
                      type="number"
                      inputMode='numeric'
                      class={{ "answer-input": true, "active": this.isActive }}
                      value={this.localNumberAnswerValue}
                      onInput={(event) => this.onNumberInput(event)}
                      disabled={this.controlDisabled}
                      onKeyDown={(event) => this.checkNumberInput(event)}
                      style={{ width: '8em' }}
                    />
                  </span>
                  <div class="triangle-topright"></div>
                </div>
              )}
              {this.fragment.type === 'QuestionString' && (
                <div class="speech-bubble-triangle">
                  <span class="speech-bubble-answer">
                    <input
                      type="text"
                      class={{ "answer-input": true, "active": this.answerValue === this.stringAnswerValue }}
                      value={this.stringAnswerValue}
                      onInput={(event) => (this.stringAnswerValue = (event.target as HTMLInputElement).value)}
                      disabled={this.controlDisabled}
                      style={{ width: '15.5em' }}
                    />
                  </span>
                  <div class="triangle-topright"></div>
                </div>
              )}
              {this.fragment.type === 'QuestionDate' && (
                <div class="speech-bubble-triangle">
                  <span class="speech-bubble-answer date">

                    <form onSubmit={(event) => {
                      event.preventDefault();
                      console.log("submit")
                      this.dateAnswerValue = this.localDateAnswerValue
                      this.setDateActive();
                      this.dateAnswer();
                    }} style={{marginRight: '0.625em'}}>

                      <input type="text" 
                      pattern="\d{4}-\d{2}-\d{2}" required placeholder="YYYY-MM-DD" 
                      value={this.localDateAnswerValue}
                      class={{ "answer-input": true, "active": this.isDateActive }}
                      disabled={this.controlDisabled}
                      onInput={(event) => this.onDateInput(event)}
                      onKeyDown={() => this.setDateActive()}
                      style={{ width: '8em' }}
                      />
                      <input type="submit" hidden />
                    </form>
                    <button id="date" onClick={(ev) => this.presentPopover(ev)}>
                    <svg viewBox={svgViewBox} width={svgWidth}>
                      <path d={svgPathCalendar} fill={svgColor} />
                    </svg>
                    </button>

                    {/*<ion-popover trigger="date">
                      <ng-template>
                        <ion-datetime
                          presentation="date"
                          first-day-of-week="1"
                          showDefaultButtons
                          locale={this.locale}
                          doneText={this.messages['confirmText']}
                          cancelText={this.messages['cancelText']}
                          onIonChange={(event) => {
                            this.handleDateChange(event.detail.value as string);
                          }}
                        ></ion-datetime>
                      </ng-template>
                        </ion-popover>*/}



                  </span>
                  <div class="triangle-topright"></div>
                </div>
              )}
            </div>
          </div>
        )}
      </div>
    );
  }

  async presentPopover(ev: Event) {
    const popover = await popoverController.create({
      component: 'ion-popover',
      event: ev,
    });
  
    //const popoverContent = document.createElement('div');
    //popoverContent.innerHTML = "Teszt"
    const popoverContent = document.createElement('ion-datetime');
    /*popoverContent.presentation = 'date';
    popoverContent.firstDayOfWeek = 1;
    popoverContent.showDefaultButtons = true;
    popoverContent.locale = this.locale;
    popoverContent.doneText = this.messages['confirmText'];
    popoverContent.cancelText = this.messages['cancelText'];
    popoverContent.addEventListener('ionChange', (event: CustomEvent) => {
      this.handleDateChange(event.detail.value);
    });*/
    popover.appendChild(popoverContent);
  
    await popover.present();
  }


  handleDateChange(dateString: string) {
    const date = new Date(dateString);
    const year = date.getFullYear();
    const month = date.getMonth() + 1;
    const day = date.getDate();
    this.localDateAnswerValue = `${year}-${this.padZero(month)}-${this.padZero(day)}`;
    this.dateAnswerValue = this.localDateAnswerValue;
    this.dateAnswer()

  }

  padZero(value: number) {
    return value.toString().padStart(2, '0');
  }

  onDateInput(event: InputEvent): void {
    this.localDateAnswerValue = (event.target as HTMLInputElement).value
    this.setDateActive();
  }
  

  setDateActive(): void {
    this.isDateActive = this.answerValue === this.dateAnswerValue && this.localDateAnswerValue === this.dateAnswerValue
    console.log("isDateActive,", this.isDateActive)
  }

  onNumberInput(event: InputEvent): void {
    this.localNumberAnswerValue = (event.target as HTMLInputElement).value
    this.setNumberActive();
  }
  checkNumberInput(event: KeyboardEvent): void {
    if (event.key === 'Enter' || event.key === 'NumpadEnter') {
      this.numberAnswerValue = (event.target as HTMLInputElement).value
      this.numberAnswer();
    }
    this.setNumberActive();
  }

  setNumberActive(): void {
    this.isActive = this.answerValue === (this.numberAnswerValue + '').replace(',', '.') && (this.localNumberAnswerValue + '').replace(',', '.') === this.numberAnswerValue
    console.log("isActive,", this.isActive)
  }


  menuAnswer(index: number): void {
    if (this.answerValue === undefined) {
      this.answerValue = index;
      this.fragment.answer = this.answerValue;
      this.answer.emit({ answer: this.answerValue, iri: this.fragment.iri });
    } else {
      this.answerValue = index;
      this.fragment.answer = this.answerValue;
      this.revokeAnswer.emit({ answer: this.answerValue, iri: this.fragment.iri });
    }
  }

  yesNoAnswer(value: string) {
    if (this.answerValue === undefined) {
      this.answerValue = value;
      this.fragment.answer = this.answerValue;
      this.answer.emit({ answer: this.answerValue, iri: this.fragment.iri });
    } else {
      this.answerValue = value;
      this.fragment.answer = this.answerValue;
      this.revokeAnswer.emit({ answer: this.answerValue, iri: this.fragment.iri });
    }
    this.yesButtonClass();
    this.noButtonClass();
  }

  numberAnswer() {
    if (!this.answerValue) {
      if (this.numberAnswerValue) {
        this.answerValue = this.numberAnswerValue.replace(',', '.')
        this.fragment.answer = this.answerValue;
        this.answer.emit({ answer: this.answerValue, iri: this.fragment.iri });
      }
    } else {
      if (this.numberAnswerValue) {
        this.answerValue = this.numberAnswerValue.replace(',', '.')
        this.fragment.answer = this.answerValue;
        this.revokeAnswer.emit({ answer: this.answerValue, iri: this.fragment.iri });
      }
    }

  }

  stringAnswer() {
    if (!this.answerValue) {
      if (this.stringAnswerValue) {
        this.answerValue = this.stringAnswerValue;
        this.fragment.answer = this.answerValue;
        this.answer.emit({ answer: this.answerValue, iri: this.fragment.iri });
      }
    } else {
      if (this.stringAnswerValue) {
        this.answerValue = this.stringAnswerValue;
        this.fragment.answer = this.answerValue;
        this.revokeAnswer.emit({ answer: this.answerValue, iri: this.fragment.iri });
      }
    }
  }

  dateAnswer() {
    if (!this.answerValue) {
      if (this.dateAnswerValue) {
        this.answerValue = this.dateAnswerValue;
        this.fragment.answer = this.answerValue;
        this.answer.emit({answer: this.answerValue, iri: this.fragment.iri});
      } 
    } else {
      if (this.dateAnswerValue) {
        this.answerValue = this.dateAnswerValue;
        this.fragment.answer = this.answerValue;
        this.revokeAnswer.emit({answer: this.answerValue, iri: this.fragment.iri});
      } 
    }

  }


  yesButtonClass(): boolean {
    let ans: string = this.answerValue + '';
    if (ans === 'undefined' || ans === 'null') {
      return false
    }

    return ans === 'true'
  }

  noButtonClass(): boolean {
    let ans: string = this.answerValue + '';
    if (ans === 'undefined' || ans === 'null') {
      return false;
    }

    return ans === 'false';
  }

  okButtonClass() {
    let ans: string = this.answerValue + '';
    if (ans === 'undefined' || ans === 'null') {
      return 'unselected-btn';
    }

    return 'selected-btn';
  }


}
