import { Observable, from } from "rxjs";
import { serverUrl } from "../../config.json";
import { ConsultationResult } from "../interfaces/dialog-interfaces";

export class ElasticService {
    private serviceUrl = `${serverUrl}/emerald/rest/api/v1/analytics/`;

    constructor() { }

    sendConsultationData(consultationObj: ConsultationResult): Observable<any> {
        return from(fetch(`${this.serviceUrl}/consultation`, {
            method: 'POST',
            body: JSON.stringify(consultationObj),
            headers: {
                'Content-Type': 'application/json'
            }
        }).then(response => response.json()));
    }

}