import { Observable, combineLatest, from } from 'rxjs';
import { map } from 'rxjs/operators';
import { serverUrl } from "../../config.json";
import { Answer, DialogData, DialogMetadata } from "../interfaces/dialog-interfaces";

export class DialogAsyncService {

    private serviceUrl: string = `${serverUrl}/emerald/rest/api/v1/product-finder`;
    sessionId: string;
    dialogIri: string;
    endpointId: string;

    constructor() { }

    startWithIri(iri: string, lang: string, answers?: Answer[], stored?: boolean): Observable<{ data: DialogData, metadata: DialogMetadata }> {
        const data$ = fetch(`${this.serviceUrl}?stored=${stored}`, {
            method: 'POST',
            body: JSON.stringify({ iri, lang, answers }),
            headers: {
                'Content-Type': 'application/json'
            }
        }).then(response => response.json()) as Promise<DialogData>;

        const metadata$ = fetch(`${this.serviceUrl}/dialogByIri?iri=${iri}&lang=${lang}`)
            .then(response => response.json()) as Promise<DialogMetadata>;

        return combineLatest([data$, metadata$]).pipe(
            map(([data, metadata]) => ({ data, metadata }))
        );

    }

    startWithCaseId(caseId: string, date: string, lang: string, answers?: Answer[], stored?: boolean): Observable<{ data: DialogData, metadata: DialogMetadata }> {
        const data$ = fetch(`${this.serviceUrl}?stored=${stored}`, {
            method: 'POST',
            body: JSON.stringify({ caseId, date, lang, answers }),
            headers: {
                'Content-Type': 'application/json'
            }
        }).then(response => response.json()) as Promise<DialogData>;

        const metadata$ = fetch(`${this.serviceUrl}/dialog?caseId=${caseId}&date=${date}`)
            .then(response => response.json()) as Promise<DialogMetadata>;

        return combineLatest([data$, metadata$]).pipe(
            map(([data, metadata]) => ({ data, metadata }))
        );
    }

    getSession(): Observable<any> {
        return from(fetch(`${this.serviceUrl}/${this.sessionId}`).then(response => response.json()));
    }

    answer(body: Answer): Observable<any> {
        return from(fetch(`${this.serviceUrl}/${this.sessionId}/answers`, {
            method: 'POST',
            body: JSON.stringify(body),
            headers: {
                'Content-Type': 'application/json'
            }
        }).then(response => response.json()));
    }

    revokeAnswer(body: Answer): Observable<any> {
        return from(fetch(`${this.serviceUrl}/${this.sessionId}/answers`, {
            method: 'PUT',
            body: JSON.stringify(body),
            headers: {
                'Content-Type': 'application/json'
            }
        }).then(response => response.json()));
    }

    getDialogMetadata(iri: string, lang: string): Observable<any> {
        return from(fetch(`${this.serviceUrl}/dialogByIri?iri=${iri}&lang=${lang}`).then(response => response.json()));
    }


}