:host {
  display: block;
}

.answer {
  display: flex;
  align-items: center;
  justify-content: flex-end;
}

.speech-bubble-triangle {
  display: flex;
  max-width: 84%;
}

.triangle-topright {
  border-top: 7px solid #bfc1c7;
  border-right: 7px solid transparent;
}

.speech-bubble-answer {
  background: #e8f1f1;
  border: 1px solid #bfc1c7;
  border-radius: 10px 0px 10px 10px;
  padding: 0.3125em;
}

.speech-bubble-answer.date{
  display: flex;
}

.answer-button {
  margin: 0.3125em;
  border: 1px solid #2b7371;
  background: #ffffff;
  border-radius: 20px;
  color: #2b7371;
  padding: 0.3125em 0.625em;
  font-weight: 600;
}

.answer-button.active,
.answer-input.active {
  background: #2b7371;
  color: #ffffff;
}

.options-container {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}

.answer-input {
  margin: 0.0625em !important;
  border: 1px solid #2b7371;
  border-radius: 2px;
  padding-left: 0.625em;
  padding-right: 0.625em;
  color: #2b7371;
  text-align: right;
  background-color: #ffffff;
}

.answer-input:focus-visible {
  border-color: #2b7371;
}

/* Chrome, Safari, Edge, Opera */
input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  -webkit-appearance: none;
  margin: 0;
}

/* Firefox */
input[type="number"] {
  -moz-appearance: textfield;
}

.fade-in.left {
  transition: 0.5s all ease-in-out;
  transform: translate3d(-50px, 0, 0);
  opacity: 0;
}

.fade-in.right {
  transition: 0.5s all ease-in-out;
  transform: translate3d(50px, 0, 0);
  opacity: 0;
}

.fade-in.show {
  opacity: 1;
  transform: translate3d(0, 0, 0);
}

.answer-input:disabled, .answer-button:disabled{
  opacity: .4;
}
